$blue : #1875e7;
$rateCardbg: #141d27;
$rate-wrapper-bg:#00a0dc;

.ql-editor{
  font-family: "Times New Roman" , Times, serif;
  font-weight:500;
  
  @media screen and (max-width: 768px) {
    font-size: 16px;
  }

  @media screen and (min-width: 769px) {
    font-size: 24px;
  }
}

#my-quill-editor{
    border-radius:5px;
    background-color: #fff;
    color: #333;
    text-align: left;
    border: 0px;
    margin-bottom: 48px;
}

#rate-card{

  .backdrop {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    //background: rgba(7,17,27,0);
    z-index: 40;
  }

  .backdrop.fade-backdrop-enter-active,

  .backdrop.fade-backdrop-leave-active{
    transition: opacity 0.5s;
  }

  .backdrop.fade-backdrop-enter,
  .backdrop.fade-backdrop-leave-active{
    opacity: 0;
  }

}

#rate-panel{

  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 48px;
  color:#fefefe;
  z-index: 50;

  .content{

    background-color: $rateCardbg;
    font-size: 16px;
    text-align: center;

    .content-flag{
        text-align: center;
        position: relative;
        height: 56px;
        line-height: 56px;
        border-radius: 50%;
        width: 56px;
        top: -10px;
        background: #141d27;
        margin: 0 12px;
        padding: 6px;
        box-sizing: border-box;

        .logo{
          background:#2b343c;
          border-radius:50%;
          font-size:24px;
          color:#80858a;
          line-height:44px;
          font-weight:700;
        }

        .active{
            background: rgb(0,160,220);
            color: white;
        }

    }

    .content-info{

       width: 100%;
       display: inline-block;

       margin-top: 12px;
       padding-right: 10px;
      text-align: left;
    }

    .content-oprations{

      background: #2b343c;
      color: #efefef;
      padding-top: 12px;

    }

    .canSubmit{
      background: #00b43c;
      color: #fff;
    }

  }


  //
  // .list-fade-enter-active {
  //   transition: all 0.5s;
  // }

  // //
  // .list-fade-leave-active {
  //  transition: all 0.5s;
  // }

  // .list-fade-leave{

  // }

  // .list-fade-enter, .list-fade-leave-active {
  //   transform: translateY(240px);
  //   //opacity: 0;
  // }


  .score-list {
    // height: 240px;
    position: absolute;
    bottom: 48px;
    width: 100%;
    background: #fff;
    z-index: -1;
    color: #07111b;

     .list-header{
        height: 40px;
        line-height: 40px;
        background: #f3f5f7;
        border-bottom: 1px solid rgba(7,17,27,.1);
        border-top: 1px solid rgba(7,17,27,.1);

        .title{
          display: inline-block;
          font-size: 14px;
          font-weight: 200;
          color: #07111b;
          padding-left: 18px;
        }

        .empty{
          float:right;
          font-size:14px;
          color: #00a0dc;
          padding:0 10px;
        }
     }

     .lsit-body{

     }

  }

}



